<script>
export default {
  data() {
    return {
      post: {},
      data: [],
      errorMessage: "",
    };
  },
  created() {
    let uri = "https://resume.maxteamdev.com/api/porthome";
    this.axios
      .get(uri)
      .then((response) => {
        this.data = response.data.data;
      })
      .catch((err) => alert(err));
  },
  methods: {
    AddPesan() {
      let uri = "https://resume.maxteamdev.com/api/porthome";
      this.axios
        .post(uri, this.post)
        .then((response) => {
          if (response.data.error) {
            this.errorMessage = response.data;
          } else {
            alert("Pesan berhasil dikirim!");
          }
        })
        .catch((err) => alert(err));
    },
  },
};
</script>