<template>
  <div>
    <main id="main">
      <!-- ======= Breadcrumbs ======= -->
      <section id="breadcrumbs" class="breadcrumbs">
        <div class="container">
          <ol>
            <li>
              <a href="index.html">Home</a>
            </li>
            <li>Portfolio Details</li>
          </ol>
          <h2>Portfolio Details</h2>
        </div>
      </section>
      <!-- End Breadcrumbs -->

      <!-- ======= Portfolio Details Section ======= -->
      <section id="portfolio-details" class="portfolio-details">
        <div class="container">
          <div class="portfolio-details-container">
            <img :src="port.url_foto" width="100%" class="img-fluid" alt />

            <div class="portfolio-info">
              <h3>Project information</h3>
              <ul>
                <li>
                  <strong>Category</strong>
                  : {{ port.nama_kategori }}
                </li>
                <li>
                  <strong>Client</strong>
                  : {{ port.klien }}
                </li>
                <li>
                  <strong>Project date</strong>
                  : {{ port.tanggal | moment("DD MMMM YYYY") }}
                </li>
                <li>
                  <strong>Project URL</strong>:
                  <a href="#">{{ port.url_project }}</a>
                </li>
              </ul>
            </div>
          </div>

          <div class="portfolio-description">
            <h2>{{ port.judul }}</h2>
            <p v-html="port.keterangan"></p>
            <carousel class="mt-5 d-flex">
              <slide v-for="fotos in port.foto" :key="fotos.id_portfolio">
                <img
                  :src="fotos.url"
                  class="img-fluid figure-img rounded"
                  alt
                /><br />
                <figcaption class="figure-caption p-2 d-flex">
                  {{ fotos.keterangan }}
                </figcaption>
              </slide>
            </carousel>
          </div>
        </div>
      </section>
      <!-- End Portfolio Details Section -->
    </main>
    <!-- End #main -->
  </div>
</template>

<script>
import { Carousel, Slide } from "vue-carousel";
export default {
  data() {
    return {
      port: {},
    };
  },
  components: {
    Carousel,
    Slide,
  },
  mounted() {
    window.scrollTo(0, 0);
  },
  created() {
    let uri = `https://resume.maxteamdev.com/api/port/show/${this.$route.params.id}`;
    this.axios
      .get(uri)
      .then((response) => {
        this.port = response.data.data;
      })
      .catch((err) => alert(err));
  },
};
</script>