<template>
  <div>
    <main id="main">
      <!-- ======= Breadcrumbs ======= -->
      <section id="breadcrumbs" class="breadcrumbs">
        <div class="container">
          <ol>
            <li>
              <a href="index.html">Home</a>
            </li>
            <li>Portfolio</li>
          </ol>
          <h2>Portfolio</h2>
        </div>
      </section>
      <!-- End Breadcrumbs -->

      <!-- ======= Portfolio Section ======= -->
      <section id="portfolio" class="portfolio section-bg">
        <div class="container" data-aos="fade-up">
          <div class="row">
            <!-- offer 01 -->

            <div
              v-for="port in data"
              :key="port.id"
              class="col-sm-12 d-flex align-items-stretch col-lg-4 mt-3"
            >
              <!-- product card -->
              <div class="product-item bg-light d-flex align-items-stretch">
                <div class="card">
                  <div class="thumb-content">
                    <!-- <div class="price">$200</div> -->
                    <a href>
                      <img
                        class="card-img-top img-fluid"
                        :src="port.url_foto"
                        alt="Card image cap"
                      />
                    </a>
                  </div>
                  <div class="card-body">
                    <h4 class="card-title">
                      <p>{{ port.judul }}</p>
                    </h4>
                    <ul class="list-inline product-meta">
                      <li
                        class="list-inline-item display-4"
                        style="font-size: 17px"
                      >
                        <i class="bx bx-folder"></i>
                        {{ port.nama_kategori }}
                      </li>
                      <li
                        class="list-inline-item display-4"
                        style="font-size: 17px"
                      >
                        <i class="bx bx-calendar"></i>
                        {{ port.created_at | moment("DD MMMM YYYY") }}
                      </li>
                    </ul>
                    <p
                      class="card-text pb-1"
                      v-html="port.keterangan.substring(0, 115) + '...'"
                    ></p>
                  </div>
                  <div class="card-footer">
                    <router-link
                      :to="{ name: 'detail', params: { id: port.id } }"
                    >
                      <div class="container mt-3">
                        <div class="row">
                          <div class="col text-center">
                            <button
                              class="mb-3"
                              style="
                                bottom: 0;
                                margin: auto;
                                background: #47b2e4;
                                border: 0;
                                padding: 12px 34px;
                                color: #fff;
                                transition: 0.4s;
                                border-radius: 50px;
                              "
                            >
                              Selengkapnya
                            </button>
                          </div>
                        </div>
                      </div>
                    </router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <!-- End Portfolio Section -->
    </main>
    <!-- End #main -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
    };
  },
  created() {
    let uri = `https://resume.maxteamdev.com/api/portfolio`;
    this.axios
      .get(uri)
      .then((response) => {
        this.data = response.data.data;
      })
      .catch((err) => alert(err));
  },
  mounted() {
    window.scrollTo(0, 0);
  },
};
</script>